import { useState } from 'react';
import PropTypes from 'prop-types';
import styles from './SearchForm.module.scss';

const SearchFrom = ({ onSearch }) => {
  const [query, setQuery] = useState('');

  // –ù–∞–±–ª—é–¥–∞–µ—Ç –∑–∞ –∏–Ω–ø—É—Ç–æ–º –∏ –ø–∏—à–µ—Ç –∑–Ω–∞—á–Ω–∏–µ –≤ —Å—Ç–µ–π—Ç
  const handleSearchInput = e => {
    const { value } = e.currentTarget;

    setQuery(value);
  };

  // –ù–∞–±–ª—é–¥–∞–µ—Ç –∑–∞ –æ—Ç–ø—Ä–∞–≤–∫–æ–π –∏ –æ—Ç–¥–∞–µ—Ç –∑–Ω–∞—á–µ–Ω–∏–µ –≤–æ –≤–Ω–µ—à–Ω–∏–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
  const handleSubmit = e => {
    e.preventDefault();

    // –ó–∞–ø—Ä–µ—â–∞–µ—Ç –æ—Ç–ø—Ä–∞–≤–∫—É –ø—É—Å—Ç–æ–≥–æ –∏–Ω–ø—É—Ç–∞
    if (!query.trim()) return;

    // –û—Ç–¥–∞—Ç—å –¥–∞–Ω–Ω—ã–µ –≤–Ω–µ—à–Ω–µ–º—É –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É
    onSearch(query);

    resetForm();
  };

  // –°–±—Ä–∞—Å—ã–≤–∞–µ—Ç –ø–æ–ª–µ –ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏
  const resetForm = () => setQuery('');

  return (
    <form className={styles.SearchForm} onSubmit={handleSubmit}>
      <button type="submit" className={styles['SearchForm-button']}>
        <span className={styles['SearchForm-button-label']}>Search</span>
      </button>

      <input
        className={styles['SearchForm-input']}
        type="text"
        name="query"
        value={query}
        onChange={handleSearchInput}
        autoComplete="off"
        autoFocus
        placeholder="Search images and photos"
      />
    </form>
  );
};

SearchFrom.propTypes = {
  onSearch: PropTypes.func.isRequired,
};

export default SearchFrom;

